import React, { useState, useRef } from 'react';
import Icon from 'components/AppIcon';
import Image from 'components/AppImage';

const PetImageUpload = ({ images, imageFiles, onImagesChange, onPrimaryImageChange, error }) => {
  const [dragActive, setDragActive] = useState(false);
  const fileInputRef = useRef(null);

  const handleDrag = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (e.type === "dragenter" || e.type === "dragover") {
      setDragActive(true);
    } else if (e.type === "dragleave") {
      setDragActive(false);
    }
  };

  const handleDrop = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      handleFiles(e.dataTransfer.files);
    }
  };

  const handleChange = (e) => {
    e.preventDefault();
    if (e.target.files && e.target.files[0]) {
      handleFiles(e.target.files);
    }
  };

  const handleFiles = (files) => {
    const fileArray = Array.from(files);
    const validFiles = fileArray.filter(file => {
      const isValidType = file.type.startsWith('image/');
      const isValidSize = file.size <= 5 * 1024 * 1024; // 5MB
      return isValidType && isValidSize;
    });

    if (validFiles.length > 0) {
      // 1. Crear URLs de previsualizacin para mostrar inmediatamente
      const newPreviews = validFiles.map(file => URL.createObjectURL(file));
      const updatedImages = [...images, ...newPreviews];
      
      // 2. Guardar los archivos reales para subirlos luego a Firebase
      const updatedFiles = [...(imageFiles || []), ...validFiles];

      // Limit to 6 images
      if (updatedImages.length <= 6) {
        onImagesChange(updatedImages, updatedFiles);
      } else {
        alert("Mximo 6 imgenes permitidas");
      }
    }
  };

  const removeImage = (index) => {
    // Si borramos una imagen, debemos borrar su preview y su archivo asociado
    // Nota: La lgica aqu asume que las imgenes nuevas estn al final. 
    // Para un MVP robusto, simplemente reconstruimos los arrays.
    
    const newImages = images.filter((_, i) => i !== index);
    
    // Si estamos borrando una imagen que es un archivo nuevo (no una URL ya existente de Firebase)
    // necesitamos saber qu ndice corresponde en imageFiles. 
    // Por simplicidad en este MVP, pasamos la lgica al padre o asumimos sincronizacin.
    // Aqu simplemente pasamos el ndice al padre y l decide cmo filtrar imageFiles.
    
    onImagesChange(newImages, null, index); // index es el que se borra
    
    if (primaryImageIndex >= newImages.length) {
      onPrimaryImageChange(Math.max(0, newImages.length - 1));
    } else if (primaryImageIndex === index) {
      onPrimaryImageChange(0);
    } else if (primaryImageIndex > index) {
      onPrimaryImageChange(primaryImageIndex - 1);
    }
  };

  return (
    <div className="space-y-6">
      <div
        className={`relative border-2 border-dashed rounded-lg p-8 text-center transition-all duration-200 ${
          dragActive
            ? 'border-primary bg-primary-50'
            : error
            ? 'border-error bg-error-light' :'border-border hover:border-primary-300 hover:bg-surface'
        }`}
        onDragEnter={handleDrag}
        onDragLeave={handleDrag}
        onDragOver={handleDrag}
        onDrop={handleDrop}
      >
        <input
          ref={fileInputRef}
          type="file"
          multiple
          accept="image/*"
          onChange={handleChange}
          className="hidden"
        />
        
        <div className="space-y-4">
          <div className={`w-16 h-16 mx-auto rounded-full flex items-center justify-center ${
            dragActive ? 'bg-primary text-white' : 'bg-surface text-primary'
          }`}>
            <Icon name="Upload" size={24} />
          </div>
          
          <div>
            <h3 className="text-lg font-medium text-text-primary mb-2">Subir fotografas</h3>
            <p className="text-text-secondary mb-4">Arrastra las imgenes aqu o haz clic para seleccionar</p>
            <button type="button" onClick={() => fileInputRef.current?.click()} className="btn-outline">
              <Icon name="Plus" size={16} className="mr-2" />
              Seleccionar imgenes
            </button>
          </div>
          
          <div className="text-sm text-text-muted">
            <p> Mximo 6 imgenes</p>
            <p> Formatos: JPG, PNG, WebP</p>
            <p> Tamao mximo: 5MB</p>
          </div>
        </div>
      </div>

      {error && (
        <p className="text-sm text-error flex items-center space-x-1">
          <Icon name="AlertCircle" size={14} /><span>{error}</span>
        </p>
      )}

      {images.length > 0 && (
        <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
          {images.map((image, index) => (
            <div key={index} className={`relative group rounded-lg overflow-hidden border-2 ${primaryImageIndex === index ? 'border-primary shadow-md' : 'border-border'}`}>
              <div className="aspect-square">
                <Image src={image} alt={`Pet ${index}`} className="w-full h-full object-cover" />
              </div>
              {primaryImageIndex === index && (
                <div className="absolute top-2 left-2 bg-primary text-white px-2 py-1 rounded text-xs font-medium flex items-center space-x-1">
                  <Icon name="Star" size={12} /><span>Principal</span>
                </div>
              )}
              <div className="absolute inset-0 bg-black bg-opacity-50 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center space-x-2">
                {primaryImageIndex !== index && (
                  <button type="button" onClick={() => onPrimaryImageChange(index)} className="bg-white text-text-primary p-2 rounded-full hover:bg-primary hover:text-white">
                    <Icon name="Star" size={16} />
                  </button>
                )}
                <button type="button" onClick={() => removeImage(index)} className="bg-white text-error p-2 rounded-full hover:bg-error hover:text-white">
                  <Icon name="Trash2" size={16} />
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default PetImageUpload;
